.header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;

    &__title {
        font-size: 40px;
    }

    &__btns {
        display: flex;
        gap: 15px;
    }

    border-bottom: 2px solid #000;
    border-radius: 1px;
}