.card {
    height: 150px;
    width: 200px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 5px;
    border: 2px solid #000;
    border-radius: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 3px;
    position: relative;
    font-weight: 700;
    transition: all 0.5s;

    @media(hover: hover) {
        &:hover {

            box-shadow: 2px 2px 0 #000;
        }
    }
}

.delete-checkbox {
    position: absolute;
    border: 1px solid #000;
    border-radius: 2px;
    position: absolute;
    left: 15px;
    top: 15px;
    width: 18px;
    height: 18px;
    cursor: pointer;
}